@import '../styles/variables';
@import '../styles/mixins';

.button {
  @include text($titleFontFamily, 14px, 1.4em, 500);
  text-transform: uppercase;
  text-decoration: none;
  cursor: pointer;
  letter-spacing: 1px;
  padding: 7px 20px;
  background: transparent;
  color: $titleTextColor;
  max-width: 170px;
  margin: 40px auto 0;
  z-index: 22;
  text-align: center;
  border: none;
  position: relative;

  &::after {
    content: '';
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    @include size(50%, 10px);
    background: $descriptionTextColor;
    z-index: -1;
    margin: 0 auto;
  }

  &:hover {
    color: $titleTextColor;

    &::after {
      height: 100%;
      width: 100%;
      border-radius: 2px;
      transition: all 0.3s ease-in-out 0s;
    }
  }
}





